se-circle-active.svg';\n$image-pause-circle-idle: '../img/pause-circle-idle.svg';\n$image-play-circle-active: '../img/play-circle-active.svg';\n$image-play-circle-idle: '../img/play-circle-idle.svg';\n\n/// MIXINS\n\n@mixin for-phone-only {\n  @media (max-width: 599px) {\n    @content;\n  }\n}\n@mixin for-tablet-portrait-up {\n  @media (min-width: 600px) {\n    @content;\n  }\n}\n\n@mixin button {\n  border: 1px solid var(--color-border);\n  cursor: pointer;\n  display: inline-block;\n  padding: 3px;\n  user-select: none;\n}\n\n/// GLOBAL\n\n:root {\n  --color-fg-error: #{$red-500};\n  --color-fg-port-closed: #{$red-500};\n  --color-fg-port-open: #{$green-500};\n  --color-progressbar-background-2: #{$grey-500};\n  --color-progressbar-verify-1: #{$pink-300};\n  --color-progressbar-verify-2: #{$pink-700};\n  --color-progressbar-magnet-1: #{$yellow-300};\n  --color-progressbar-magnet-2: #{$yellow-700};\n  --color-progressbar-magnet-paused-1: #{$default-accent-color};\n  --color-progressbar-magnet-paused-2: #{$grey-500};\n  --color-progressbar-leech-paused-1: #{$blue-200};\n  --color-progressbar-leech-paused-2: #{$grey-500};\n  --color-progressbar-leech-queued-1: #{$blue-100};\n  --color-progressbar-leech-queued-2: #{$blue-300};\n  --color-progressbar-leech-1: #{$blue-300};\n  --color-progressbar-leech-2: #{$blue-700};\n  --color-progressbar-seed-paused-1: #{$green-200};\n  --color-progressbar-seed-paused-2: #{$grey-500};\n  --color-progressbar-seed-queued-1: #{$green-100};\n  --color-progressbar-seed-queued-2: #{$green-300};\n  --color-progressbar-seed-1: #{$green-300};\n  --color-progressbar-seed-2: #{$green-700};\n  color-scheme: light dark;\n  @media (prefers-color-scheme: dark) {\n    --color-bg-hover: #{$grey-40};\n    --color-fg-primary: #fff;\n    --color-bg-primary: #{$grey-800};\n    --color-bg-odd: #{$grey-900};\n    --color-bg-even: #{$grey-800};\n    --color-bg-menu: #{$grey-800};\n    --color-fg-secondary: #{$nice-grey};\n    --color-fg-on-popup: #{$nice-grey};\n    --color-fg-disabled: #{$nice-grey};\n    --color-bg-popup: #{$grey-800};\n    --color-bg-warn: #cf6679;\n    --color-fg-warn: #{$dark-mode-black};\n    --color-border: #{$nice-grey};\n    --color-border-selected: #d0d7de;\n    --color-fg-tertiary: #{$grey-500};\n    --color-toolbar-background: #{$grey-800};\n    --color-inspector-background: #{$grey-800};\n    --color-inspector-tabs: #{$nice-grey};\n    --color-bg-selected: #{$default-accent-color-dark};\n    --color-bg-tabs: #{$grey-700};\n    --color-progressbar-background-1: #426389;\n    --color-default-border: #{$default-border-dark};\n  }\n  @media (prefers-color-scheme: light) {\n    --color-bg-hover: #{$nice-grey};\n    --color-fg-primary: #404040;\n    --color-bg-primary: #fff;\n    --color-bg-even: #{$white};\n    --color-bg-odd: #{$nice-grey};\n    --color-bg-menu: #{$nice-grey};\n    --color-fg-on-popup: #{$grey-900};\n    --color-bg-popup: #{$white};\n    --color-fg-secondary: #{$grey-500};\n    --color-border: #{$grey-500};\n    --color-border-selected: #d0d7de;\n    --color-fg-tertiary: #{$grey-500};\n    --color-bg-warn: #e4606d5b;\n    --color-fg-warn: #cf212e;\n    --color-fg-disabled: #{$grey-500};\n    --color-toolbar-background: #{$nice-grey};\n    --color-inspector-background: #{$white};\n    --color-bg-tabs: #{$nice-grey-darker};\n    --color-inspector-tabs: #{$nice-grey};\n    --color-bg-selected: #{$default-accent-color};\n    --color-progressbar-background-1: #{$nice-grey};\n    --color-default-border: #{$default-border-light};\n    --color-dialog-border: #{$nice-grey};\n  }\n}\n\nhtml,\nbody {\n  color: var(--color-fg-primary);\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,\n    Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';\n  height: 100%;\n  margin: 0;\n}\n\nimg {\n  border: 0;\n}\n\na {\n  color: var(--color-fg-primary);\n  outline: 0;\n  text-decoration: underline;\n}\n\n.hidden {\n  display: none;\n}\n\n.even {\n  background-color: var(--color-bg-even);\n}\n\n.odd {\n  background-color: var(--color-bg-odd);\n}\n\n.mainwin {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n  position: relative;\n\n  > * {\n    margin: 0;\n    padding: 0;\n  }\n}\n\n.mainwin-workarea {\n  display: flex;\n  flex: 1;\n  flex-direction: row;\n  overflow: auto;\n}\n\ninput {\n  border: 1px solid var(--color-border);\n  border-radius: 3px;\n  padding: 4px 5px;\n}\n\n/// TOOLBAR\n\n$toolbar-height-number: 50;\n$toolbar-height: $toolbar-height-number * 1px;\n\n#mainwin-toolbar {\n  align-items: center;\n  background: var(--color-toolbar-background);\n  border-bottom: 1px solid var(--color-default-border);\n  display: flex;\n  flex-direction: row;\n  height: $toolbar-height;\n  margin: 0;\n  width: 100%;\n\n  button {\n    background-color: transparent;\n    background-position: center;\n    background-repeat: no-repeat;\n    border: 0;\n    border-radius: 5px;\n    cursor: pointer;\n    height: $toolbar-height;\n    margin-right: 6px;\n    user-select: none;\n    width: $toolbar-height;\n\n    svg {\n      stroke: var(--color-fg-primary); // ios only? idk yet\n    }\n\n    &:disabled {\n      cursor: default;\n      opacity: 0.25;\n    }\n  }\n\n  > .toolbar-separator {\n    border-left: 1px solid var(--color-default-border);\n    height: 25px;\n    margin: 0 6px 0 0;\n  }\n\n  :nth-last-child(2) {\n    border: 0;\n    flex-grow: 1;\n  }\n}\n\n.toolbar-icon {\n  stroke: var(--color-fg-primary);\n}\n\n#toolbar-inspector {\n  svg {\n    color: var(--color-fg-primary);\n  }\n}\n\n#toolbar-overflow {\n  margin-right: 4px;\n\n  svg {\n    fill: var(--color-fg-primary);\n    height: 36px;\n    width: 36px;\n  }\n\n  &.alt-speed-enabled {\n    background-image: url($image-alt-speed-enabled);\n    background-position: bottom left;\n    background-size: 20px;\n  }\n}\n\n/// FILTERBAR\n\n.mainwin-filterbar {\n  align-items: center;\n  background: var(--color-toolbar-background);\n  border-bottom: 1px solid var(--color-default-border);\n  display: flex;\n  flex-direction: row;\n  height: 30px;\n  width: 100%;\n\n  @include for-phone-only {\n    padding: 5px 10px;\n\n    :not(select):not(input):not(option) {\n      display: none;\n    }\n\n    #torrent-search {\n      padding: 5px;\n    }\n  }\n\n  > * {\n    margin-right: 5px;\n    padding: 0 5px;\n  }\n}\n\n$speed-icon-size: 12px;\n\n.speed-up-icon {\n  background-image: url($image-chevron-up);\n  background-repeat: no-repeat;\n  background-size: $speed-icon-size $speed-icon-size;\n  height: $speed-icon-size;\n  width: $speed-icon-size;\n}\n\n.speed-dn-icon {\n  background-image: url($image-chevron-dn);\n  background-repeat: no-repeat;\n  background-size: $speed-icon-size $speed-icon-size;\n  height: $speed-icon-size;\n  width: $speed-icon-size;\n}\n\n/// TORRENT CONTAINER\n\n$pauseresume-size-number: 20;\n$pauseresume-size: $pauseresume-size-number * 1px;\n$progressbar-border-number: 1;\n$progressbar-height-number: 14;\n$popup-top: 61px; // TODO: ugly that this is hardcoded\n\n.flex {\n  flex-grow: 1;\n}\n\n#torrent-container {\n  flex-grow: 1;\n  left: 0;\n  margin: 0;\n  overflow: auto;\n  -webkit-overflow-scrolling: touch;\n  padding: 0;\n  right: 0;\n}\n\n.torrent-container {\n  background-color: $white;\n}\n\n$app-image: '../img/box.svg';\n$audio-image: '../img/music.svg';\n$folder-image: '../img/folder.svg';\n$font-image: '../img/type.svg';\n$image-image: '../img/image.svg';\n$package-image: '../img/package.svg';\n$text-image: '../img/file-text.svg';\n$video-image: '../img/film.svg';\n\n@mixin mime-icon-compact($mime-icon) {\n  &[data-icon-multifile='true'] {\n    background-image: url($folder-image);\n    background-position: center;\n    background-size: 16px;\n  }\n}\n\n@mixin mime-icon($mime-icon) {\n  // background-image: url($mime-icon);\n  background-color: var(--color-fg-primary);\n  -webkit-mask-image: url($mime-icon);\n  mask-image: url($mime-icon);\n  -webkit-mask-repeat: no-repeat;\n  mask-repeat: no-repeat;\n\n  &[data-icon-multifile='true'] {\n    -webkit-mask-image: url($mime-icon), url($folder-image);\n    mask-image: url($mime-icon), url($folder-image);\n    -webkit-mask-position: top left, bottom right;\n    mask-position: top left, bottom right;\n    -webkit-mask-repeat: no-repeat, no-repeat;\n    mask-repeat: no-repeat, no-repeat;\n    -webkit-mask-size: 16px, 16px;\n    mask-size: 16px, 16px;\n  }\n}\n\n.torrent-list {\n  cursor: pointer;\n  list-style-type: none;\n  margin: 0;\n  padding: 0;\n  text-align: left;\n  width: 100%;\n\n  .torrent {\n    border-bottom: 1px solid var(--color-default-border);\n    user-select: none;\n\n    .icon {\n      // color the background svg fill\n      background-color: var(--color-fg-primary);\n      background-position: center, top left;\n\n      &[data-icon-mime-type='audio'] {\n        @include mime-icon($audio-image);\n      }\n\n      &[data-icon-mime-type='application'] {\n        @include mime-icon($app-image);\n      }\n\n      &[data-icon-mime-type='font'] {\n        @include mime-icon($font-image);\n      }\n\n      &[data-icon-mime-type='image'] {\n        @include mime-icon($image-image);\n      }\n\n      &[data-icon-mime-type='model'] {\n        @include mime-icon($package-image);\n      }\n\n      &[data-icon-mime-type='text'] {\n        @include mime-icon($text-image);\n      }\n\n      &[data-icon-mime-type='video'] {\n        @include mime-icon($video-image);\n      }\n    }\n\n    &.compact {\n      $icon-size-num: 16;\n      $icon-size: $icon-size-num * 1px;\n\n      align-items: center;\n      display: flex;\n      flex-direction: row-reverse;\n\n      .icon {\n        flex-shrink: 0;\n        height: $icon-size;\n        -webkit-mask-size: $icon-size, $icon-size-num * 0.5px;\n        mask-size: $icon-size, $icon-size-num * 0.5px;\n        width: $icon-size;\n\n        &[data-icon-multifile='true'] {\n          @include mime-icon($folder-image);\n        }\n      }\n\n      > * {\n        margin: 5px;\n      }\n    }\n\n    &:not(.compact) {\n      $icon-size-num: 32;\n      $icon-size: $icon-size-num * 1px;\n      align-items: center;\n      display: grid;\n      grid-column-gap: 12px;\n      grid-template-areas:\n        'icon name labels'\n        'icon peers peers'\n        'icon progressbar progressbar'\n        'icon progress-text progress-text';\n      grid-template-columns: $icon-size auto 1fr;\n      padding: 6px 12px;\n\n      .icon {\n        background-size: $icon-size, $icon-size-num * 0.5px;\n        grid-area: icon;\n        height: $icon-size;\n        width: $icon-size;\n      }\n\n      .torrent-name {\n        color: var(--color-fg-primary);\n        grid-area: name;\n\n        &.paused {\n          color: var(--color-fg-disabled);\n        }\n      }\n\n      .torrent-labels {\n        grid-area: labels;\n      }\n\n      .torrent-peer-details {\n        grid-area: peers;\n      }\n\n      .torrent-progress {\n        display: flex;\n        flex-direction: row;\n        grid-area: progressbar;\n      }\n\n      .torrent-progress-details {\n        grid-area: progress-text;\n      }\n\n      > * {\n        margin: 1px 0;\n      }\n    }\n\n    &.selected {\n      background-color: var(--color-bg-selected);\n    }\n  }\n\n  .icon {\n    background-position: center;\n    background-repeat: no-repeat;\n  }\n\n  .torrent-pauseresume-button {\n    background-position: center center;\n    background-repeat: no-repeat;\n    background-size: $pauseresume-size;\n    margin-left: math.floor(math.div($pauseresume-size-number, 2)) * 1px;\n    width: $pauseresume-size;\n\n    &[data-action='pause'] {\n      background-image: url($image-pause-circle-idle);\n\n      &:active,\n      &:hover {\n        background-image: url($image-pause-circle-active);\n      }\n    }\n\n    &[data-action='resume'] {\n      background-image: url($image-play-circle-idle);\n\n      &:active,\n      &:hover {\n        background-image: url($image-play-circle-active);\n      }\n    }\n  }\n\n  .torrent-progress-details.error,\n  .torrent-peer-details.error {\n    color: var(--color-fg-error);\n  }\n\n  .torrent-name {\n    font-weight: bold;\n    margin-bottom: 2px;\n    margin-top: 2px;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n\n    &.compact {\n      font-size: 1em;\n      font-weight: normal;\n    }\n\n    &:not(.paused) {\n      color: var(--color-fg-primary);\n    }\n  }\n\n  .torrent-labels {\n    font-size: x-small;\n    font-weight: normal;\n    margin-bottom: 2px;\n    margin-top: 2px;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n\n    &.compact {\n      flex: 1;\n      font-size: x-small;\n    }\n  }\n\n  .torrent-progress-details,\n  .torrent-peer-details {\n    color: var(--color-fg-primary);\n    font-size: x-small;\n  }\n\n  .torrent-progress-bar {\n    background-repeat: no-repeat;\n    border-color: var(--color-border-selected);\n    border-radius: 3px;\n    border-style: solid;\n    border-width: 1px;\n    height: ($progressbar-height-number - ($progressbar-border-number * 2)) *\n      1px;\n\n    &.full {\n      flex-grow: 1;\n      margin: 2px 0;\n    }\n\n    &.compact {\n      min-width: 50px;\n      width: 50px;\n    }\n\n    @function progressbar-gradient($color1, $color2) {\n      @return linear-gradient(to bottom, $color1, $color2 80%);\n    }\n\n    @mixin progressbar-background($color1, $color2) {\n      background: progressbar-gradient($color1, $color2),\n        progressbar-gradient(\n          var(--color-progressbar-background-1),\n          var(--color-progressbar-background-1)\n        );\n      background-repeat: no-repeat;\n    }\n\n    &.leech.paused {\n      @include progressbar-background(\n        var(--color-progressbar-leech-paused-1),\n        var(--color-progressbar-leech-paused-1)\n      );\n    }\n\n    &.leech.queued {\n      @include progressbar-background(\n        var(--color-progressbar-leech-queued-1),\n        var(--color-progressbar-leech-queued-1)\n      );\n    }\n\n    &.leech {\n      @include progressbar-background(\n        var(--color-progressbar-leech-1),\n        var(--color-progressbar-leech-1)\n      );\n    }\n\n    &.magnet.paused {\n      @include progressbar-background(\n        var(--color-progressbar-magnet-paused-1),\n        var(--color-progressbar-magnet-paused-1)\n      );\n    }\n\n    &.magnet {\n      @include progressbar-background(\n        var(--color-progressbar-magnet-1),\n        var(--color-progressbar-magnet-1)\n      );\n    }\n\n    &.seed.paused {\n      @include progressbar-background(\n        var(--color-progressbar-seed-paused-1),\n        var(--color-progressbar-seed-paused-1)\n      );\n    }\n\n    &.seed.queued {\n      @include progressbar-background(\n        var(--color-progressbar-seed-queued-1),\n        var(--color-progressbar-seed-queued-1)\n      );\n    }\n\n    &.seed {\n      @include progressbar-background(\n        var(--color-progressbar-seed-1),\n        var(--color-progressbar-seed-1)\n      );\n    }\n\n    &.verify {\n      @include progressbar-background(\n        var(--color-progressbar-verify-1),\n        var(--color-progressbar-verify-1)\n      );\n    }\n  }\n}\n\n/// PREFERENCES DIALOG\n\n@include for-phone-only {\n  html,\n  body {\n    overflow: hidden;\n  }\n\n  input[type='checkbox'] {\n    height: 20px;\n    width: 20px;\n  }\n}\n\n#inspector,\n#prefs-dialog {\n  @include for-phone-only {\n    left: 0;\n  }\n  @include for-tablet-portrait-up {\n    border-left: 1px solid var(--color-default-border);\n    width: 570px;\n  }\n}\n\n#prefs-dialog {\n  $background-color: var(--color-inspector-background);\n  background: $background-color;\n  border: 0;\n  bottom: 0;\n  box-shadow: 0 3px 6px -4px #0000001f, 0 6px 16px #00000014,\n    0 9px 28px 8px #0000000d;\n  position: absolute;\n  right: 0;\n  top: $popup-top;\n  z-index: $z-index-popup;\n\n  .tabs-page {\n    grid-column-gap: 8px;\n  